import type { HTMLAttributes, PropsWithChildren } from 'react';

import classNames from 'classnames';
import './index.scss';
// ---------------------------------------------------------------------- //

export type HeadBaseProps = PropsWithChildren & HTMLAttributes<HTMLBaseElement>;

const HeadBase = ({ children, className }: HeadBaseProps) => {
  return <strong className={classNames('head-base', className)}>{children}</strong>;
};

export default HeadBase;
