import type { HTMLAttributes, PropsWithChildren } from 'react';

import classNames from 'classnames';
import './index.scss';
// ---------------------------------------------------------------------- //

export type BodySmallProps = PropsWithChildren & HTMLAttributes<HTMLSpanElement>;

const BodySmall = ({ children, className }: BodySmallProps) => {
  return <span className={classNames('body-sm', className)}>{children}</span>;
};

export default BodySmall;
