import type { HTMLAttributes, PropsWithChildren } from 'react';

import classNames from 'classnames';
import './index.scss';
// ---------------------------------------------------------------------- //

export type HeadMediumProps = PropsWithChildren & HTMLAttributes<HTMLSpanElement>;

const HeadMedium = ({ children, className }: HeadMediumProps) => {
  return <strong className={classNames('head-medium', className)}>{children}</strong>;
};

export default HeadMedium;
