import type { HTMLAttributes, PropsWithChildren } from 'react';

import classNames from 'classnames';
import './index.scss';
// ---------------------------------------------------------------------- //

export type BodyLargeProps = PropsWithChildren & HTMLAttributes<HTMLSpanElement>;

const BodyLarge = ({ children, className }: BodyLargeProps) => {
  return <span className={classNames('body-large', className)}>{children}</span>;
};

export default BodyLarge;
