import type { HTMLAttributes, PropsWithChildren } from 'react';

import classNames from 'classnames';
import './index.scss';
// ---------------------------------------------------------------------- //

export type BodyMediumProps = PropsWithChildren & HTMLAttributes<HTMLSpanElement>;

const BodyMedium = ({ children, className }: BodyMediumProps) => {
  return <span className={classNames('body-medium', className)}>{children}</span>;
};

export default BodyMedium;
