import type { HTMLAttributes, PropsWithChildren } from 'react';

import classNames from 'classnames';
import './index.scss';
// ---------------------------------------------------------------------- //

export type HeadLargeProps = PropsWithChildren & HTMLAttributes<HTMLSpanElement>;

const HeadLarge = ({ children, className }: HeadLargeProps) => {
  return <strong className={classNames('head-large', className)}>{children}</strong>;
};

export default HeadLarge;
