import type { HTMLAttributes, PropsWithChildren } from 'react';

import './index.scss';
import classNames from 'classnames';
// ---------------------------------------------------------------------- //

export type BodyBaseProps = PropsWithChildren & HTMLAttributes<HTMLSpanElement>;

const BodyBase = ({ children, className }: BodyBaseProps) => {
  return <span className={classNames('body-base', className)}>{children}</span>;
};

export default BodyBase;
