import type { HTMLAttributes, PropsWithChildren } from 'react';

import classNames from 'classnames';
import './index.scss';
// ---------------------------------------------------------------------- //

export type HeadSmallProps = PropsWithChildren & HTMLAttributes<HTMLSpanElement>;

const HeadSmall = ({ children, className }: HeadSmallProps) => {
  return <strong className={classNames('head-sm', className)}>{children}</strong>;
};

export default HeadSmall;
